._3FIL- {
  color: #777;
  padding: 10px 15px;
  height: 20px;
  text-align: center;
  border-top: 1px solid #e6e6e6;
}

._3FIL-:before {
  content: '';
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: 50px;
  overflow: hidden;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2), 0 8px 0 -3px #f6f6f6, 0 9px 1px -3px rgba(0, 0, 0, 0.2),
    0 16px 0 -6px #f6f6f6, 0 17px 2px -6px rgba(0, 0, 0, 0.2);
}

._1BcvS {
  margin: 0;
  padding: 0;
  list-style: none;
  position: absolute;
  right: 0;
  left: 0;
}

._1BcvS li {
  display: inline;
}

._1BcvS li a {
  color: inherit;
  margin: 3px;
  padding: 3px 7px;
  text-decoration: none;
  border: 1px solid transparent;
  border-radius: 3px;
}

._1BcvS li a._2piwY,
._1BcvS li a:hover {
  border-color: rgba(175, 47, 47, 0.1);
}

._1BcvS li a._2piwY {
  border-color: rgba(175, 47, 47, 0.2);
}

._3khYx {
  float: left;
  text-align: left;
}

._3khYx strong {
  font-weight: 300;
}

._3tf6X,
html ._3tf6X:active {
  float: right;
  position: relative;
  line-height: 20px;
  text-decoration: none;
  cursor: pointer;
  position: relative;
}

._3tf6X:hover {
  text-decoration: underline;
}

